import { useRefFrame } from '@anonymous.club/ui';
import { useRef } from 'react';
import { MeshPhysicalMaterial } from 'three';
import { WorldStateConsumer } from '@anonymous.club/types';
import { useSpring, animated } from '@react-spring/three';

export const ClearOnHoverMaterial = ({ useWorldStore, ...props }: Required<WorldStateConsumer> & JSX.IntrinsicElements['meshPhysicalMaterial']) => {
  const ref = useRef<MeshPhysicalMaterial>();
  const isHeroHovered = useWorldStore(state => 0.3 + 0.7 * ~~!state.isHeroHovered);
  const { alpha } = useSpring({ alpha: isHeroHovered });
  useRefFrame(([mesh]) => {
    mesh.opacity = alpha.get();
  }, [ref]);
  return (
    <animated.meshPhysicalMaterial
      {...props}
      ref={ref}
      />
  );
};
