import { BoxProps, Physics, PlaneProps, useBox, usePlane } from '@react-three/cannon';
import { Canvas } from '@react-three/fiber';
import { Route, Link } from 'react-router-dom';
import './jsx-extensions';
import './WorldEnvironment.module.scss';
import {
  DoubleSide,
  VSMShadowMap,
  ReinhardToneMapping,
  ACESFilmicToneMapping,
  Vector3
} from 'three';
import { Suspense } from 'react';
import { UseStore } from 'zustand';
import { WorldState } from '@anonymous.club/types';
import { EnvironmentEffects } from './EnvironmentEffects';
import { WorldLights } from './WorldLights';
import { InternalCamera } from './InternalCamera';
/* eslint-disable-next-line */
export interface WorldEnvironmentProps {
  useWorldStore: UseStore<WorldState>;
  children?: React.ReactNode;
}

function Plane(props: PlaneProps) {
  const [ref] = usePlane(() => ({ material: {restitution: 0.994}, ...props}));
  // useFrame(({clock}) => {
  //   // ref.current.rotation.x++
  // });
  return (
    <mesh
      receiveShadow
      ref={ref}
      >
      <planeBufferGeometry
        args={props.args}
        attach="Geometry "
        />
      <shadowMaterial
        attach="material"
        color="black"
        opacity={0.82}
        />
    </mesh>
  );
}

function Box(props: BoxProps) {
  const [ref] = useBox(() => ({ material: {restitution: 0.94}, ...props}));
  return (
    <mesh
      castShadow
      receiveShadow
      ref={ref}
      >
      <boxBufferGeometry
        args={props.args}
        attach="geometry"
        />
      <meshLambertMaterial
        attach="material"
        color="hotpink"
        side={DoubleSide}
        />
    </mesh>
  );
}
export function WorldEnvironment({useWorldStore,children}: WorldEnvironmentProps) {
  // const shouldStop = useWorldStore(state => state.isHeroHovered);
  return (
    <Canvas
      // orthographic
      // camera={{ fov: 45, far: 10000 }}
      camera={{ fov: 22.5, far: -1 }}
      gl={{ alpha: true, toneMapping: ACESFilmicToneMapping, physicallyCorrectLights: false, powerPreference: 'high-performance' }}
      >
      {/* <gridHelper
        args={[400, 40]}
        position-y={-100}
        />
      <polarGridHelper args={[800, 64, 32, 64, 0x000000, 0x000000]} /> */}
      <InternalCamera />
      <color
        args={[0xffffff - 0x010100]}
        // args={[0x010100]}
        attach="background"
        />
      <WorldLights {...{useWorldStore}} />
      {children}
      <EnvironmentEffects />
    </Canvas>
  );
}

export default WorldEnvironment;
