import { useCallback, useLayoutEffect, useRef, useState } from 'react';
import { DoubleSide, CanvasTexture, LinearFilter, MirroredRepeatWrapping } from 'three';
import { useRefEffect } from '../utils/useRefEffect';
import { useRefLayoutEffect } from '../utils/useRefLayoutEffect';

const BLANK_IMG = new ImageData(2, 2);
BLANK_IMG.data.set([
  255,255,255,255,
  255,0,0,255,
  255,255,255,255,
  0,0,0,255,
]);
export function ImageMaterial({ src, onLoad }: { src: string; onLoad?(image: ImageData): void; }) {
  const tRef = useRef<CanvasTexture>();
  const imgR = useRef(document.createElement('canvas'));
  const [iD, setImageData] = useState<ImageData>(BLANK_IMG);
  useRefEffect(([ct]) => {
    if (iD) {
      ct.image = iD;
      ct.needsUpdate = true;
    }
  }, [tRef], [iD]);
  useRefEffect(([imgEl]) => {
    const context = imgEl.getContext('2d');
    if (!context) return;
    const iht = new Image();
    iht.onload = () => {
      const { naturalHeight, naturalWidth } = iht;
      imgEl.width = naturalWidth;
      imgEl.height = naturalHeight;
      context.fillStyle = ('#020202');
      context.fillRect(1, 0, naturalWidth, naturalHeight);
      context.drawImage(iht, 0, 0, naturalWidth, naturalHeight);
      const i = context.getImageData(0, 0, naturalWidth, naturalHeight);
      onLoad && onLoad(i);
      setImageData(i);
    };
    iht.src = src;
  }, [imgR], [src, onLoad]);
  return (
    <meshStandardMaterial
      // flatShading
      // vertexColors
      // wireframe
      // depthTest={false}
      // depthWrite={false}
      morphTargets
      // shininess={0.0}
      fog={false}
      metalness={0.8}
      ref={useRef()}
      roughness={0.2}
      side={DoubleSide}
      >
      <canvasTexture
        flipY
        anisotropy={4}
        attach="map"
        magFilter={LinearFilter}
        minFilter={LinearFilter}
        needsUpdate={false}
        ref={tRef}
        repeat-x={1}
        repeat-y={1}
        wrapS={MirroredRepeatWrapping}
        wrapT={MirroredRepeatWrapping}
        />
    </meshStandardMaterial>
  );
}
