import { useMemo } from "react";
import { useThreeGl } from "./useThreeGl";

export const useBufferHeight = () => {
  const gl = useThreeGl();
  const {
    drawingBufferHeight: height,
    drawingBufferWidth: width,
  } = useMemo(() => gl.getContext(), [gl]);
  const max = useMemo(() => Math.max(height, width), [height, width]);
  const min = useMemo(() => Math.min(height, width), [height, width]);
  const diag = useMemo(() => (width ** 2 + height ** 2) ** 0.5, [height, width]);
  return {height, width, max, min, diag};
};
