export function pluck<T extends string>(key: T) {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  return <O extends Record<T, any>, R = O extends Record<T, infer U> ? U : never>({ [key]: value }: O): R => value;
}

export const getGl = pluck('gl');
export const getCamera = pluck('camera');
