import { UseStore } from 'zustand';
import { CirculatingLight } from './CirculatingLight';
import { SHADOW_SCALE } from './SHADOW_SCALE';
import { WorldState } from '@anonymous.club/types';
import { StaticLight } from './StaticLight';
import { useBufferHeight } from '../utils';
export function WorldLights({ useWorldStore }: { useWorldStore: UseStore<WorldState>; }) {
  const shouldStop = useWorldStore(state => state.isHeroHovered);
  const shouldShowBottom = useWorldStore(state => state.shouldShowBottom);
  const buffer = useBufferHeight();
  return (
    <>
      <hemisphereLight
        // color="white"
        // groundColor="lightblue"
        intensity={0.05}
        />
      <CirculatingLight
        axis={[.1, .1, 1]}
        center={[140, -40, -100]}
        // color="gold"
        decay={2}
        freq={0.22}
        intensity={1}
        layers={1}
        // shadow-bias={-0.00002}
        radius={60}
        shadow-camera-far={-1}
        shadow-mapSize-height={SHADOW_SCALE}
        shadow-mapSize-width={SHADOW_SCALE}
        />
      <CirculatingLight
        axis={[2, 0.2, 1]}
        center={[0, 10, 40]}
        color={0xfdfffc}
        // color={0xff0000}
        decay={30}
        distance={0}
        freq={0.35}
        intensity={2}
        layers={0}
        offset={3.14}
        radius={buffer.diag / 2}
        shadow-bias={-0.0004}
        shadow-camera-far={-1}
        shadow-mapSize-height={SHADOW_SCALE}
        shadow-mapSize-width={SHADOW_SCALE}
        // shouldStop={shouldStop}
        />
      <spotLight
        castShadow
        angle={1.0}
        intensity={0.5}
        layers={1}
        position={[100, 200, 300]}
        shadow-bias={-0.00002}
        shadow-mapSize-height={SHADOW_SCALE}
        shadow-mapSize-width={SHADOW_SCALE}
        />
      <StaticLight
        // color={'hotpink'}
        // depthBuffer={depthRef}
        shouldShowBottom={shouldShowBottom}
        />
      <spotLight
        castShadow
        angle={1.0}
        decay={0}
        intensity={0.5}
        layers={1}
        position={[-100, 200, 300]}
        shadow-bias={-0.00002}
        shadow-mapSize-height={SHADOW_SCALE}
        shadow-mapSize-width={SHADOW_SCALE}
        />
    </>
  );
}
