/* eslint-disable-next-line */
export interface GlobeProps {
  imgs: string[]
  cols?: number
}

export function Globe(props: GlobeProps) {
  const {imgs, cols} = props;
  const numCols = Math.min(imgs.length, cols ?? 4);
  const numRows = Math.ceil(imgs.length / numCols);
  return (
    <>
      {imgs.slice(0, numCols * numRows).map((src, i) => (
        <Suspense
          fallback={null}
          key={src + '?' + numCols + 'x' + numRows + 'x' + ~~(i / numCols)}
          >
          <GlobeItem {...{src, index: i, numCols, numRows}} />
        </Suspense>
      ))}
    </>
  );
}

export default Globe;
const verticalSlice = (divisions = 1, gutter = 0) => {
  const dStart = 0.75 * Math.PI;
  const dDelta = 0.5 * Math.PI / divisions;
  return (cell: number) => {
    return [0, 1].map((i) =>  dStart + dDelta * (cell + i) - (i * gutter));
  };
};

type GlobeItemOptionals = {
  index?: number;
} & {
  position?: [row: number, col: number];
};

type GlobeItemProps = GlobeItemOptionals & {
  src: string;
  size?: number;
  numRows: number;
  numCols: number;
};
type GlobeItemKeys = keyof GlobeItemProps;
function GlobeItem(props: GlobeItemProps) {
  const gutter = 0.01;
  const {src, size = 250, numCols, numRows} = props;
  const {index = 0} = props;
  const {position: [pX, pY] = [~~(index /numCols) % numRows, index % numCols]} = props;
  const NUM_ITEM_POINTS = 16;
  const halfDim = size / 2;
  const [dim, setDim] = useState(1);
  const [{pH}] = useSpring(() => ({ pH: 0, config: {tension: 210, friction: 40, bounce: 0.5}}));
  const [{pos, mH}] = useSpring(() => ({pos: 1, mH: 1, config: { tension: 120, friction: 60, clamp: true }}));
  const buffer = useBufferHeight();
  const afterImageLoads = useCallback((img: ImageData) => {
    pH.start(1);
    pos.start(0);
    mH.start(1);
    // setDim((img.height / img.width) / (buffer.height / buffer.width));
    setDim((img.height / img.width));
    // pH.start((img.width / img.height) ** 1);
  }, [mH, pH, pos]);
  const ref = useRef<Group>();
  const lref = useRef<LatheBufferGeometry>();

  useLayoutEffect(() => {
    console.log(pX, pY, numCols, numRows, (pX - (numRows - 1) / 2));
    if (ref.current) {
      ref.current.layers.set(0);
      // console.log(ref.current.getWorldPosition(new Vector3()));
    };
    mH.start(1);
  }, [pos, pH, src, pX, pY, numCols, numRows, mH]);

  const sliceFn = useMemo(() => verticalSlice(1, gutter), []);
  const sliceFn2 = useMemo(() => verticalSlice(numRows, gutter), [numRows]);
  const points = useMemo(() => {
    const bigR = buffer.diag;
    // const curve = new EllipseCurve(0, 0, 2 * bigR - 5, 2 * bigR - 5, 0.75 * Math.PI, 1.25 * Math.PI, false, 0);
    const [phiStart, phiEnd] = sliceFn(0);
    const curve = new EllipseCurve(-bigR * 0.5, 0, 2 * bigR - 5, 2 * bigR - 5, phiStart, phiEnd, false, 0);
    const curvePoints = curve.getSpacedPoints(NUM_ITEM_POINTS);
    return curvePoints;
  }, [buffer.diag, sliceFn]);

  const origin = useMemo(() => new Vector3(0, 0, 1), []);
  const head = useMemo(() => new Vector3(), []);
  const cam = useThreeCamera();
  // const posCenter = new Vector3(0, 0, buffer.diag);
  const posCenter = new Vector3(0, 0, -buffer.diag * 0);
  const mRef = useRef<Mesh>();
  useRefFrame(([obj]) => {
    const { position } = obj;
    position.lerpVectors(posCenter, cam.position.clone().multiplyScalar(-1), pos.get());
    if (obj.morphTargetInfluences) {
      obj.morphTargetInfluences[0] = mH.get();
    }
    else obj.updateMorphTargets();
    // obj?.lookAt(posCenter);
  }, [mRef]);
  const xStart = 0.5 * Math.PI;
  const xDelta = Math.PI / (numCols / 2);
  const yStart = 0.5 * Math.PI;
  const yDelta = 2 * Math.PI / numRows;
  const { rD, rM, rX, rZ } = useSpring({
    rD: 0.25 * Math.PI - xDelta * pY,
    // rM: (pX - (numRows - 1) / 2)
    rM: (0.1 * buffer.height * (pX - (numRows - 1) / 2)),
    rZ: (buffer.height * (numRows - (pX - (numRows - 1) / 2))),
    rX: Math.PI * 0.75 * (pX - (numRows - 1) / 2),
    // rX: -(yStart - yDelta * (pX - (numRows - 1) / 2))
  });
  useRefEffect(([geometry]) => {
    const position = geometry.getAttribute('position');
    if (!geometry.morphAttributes.position) geometry.morphAttributes.position = [];
    geometry.computeBoundingBox();
    geometry.boundingSphere ?? geometry.computeBoundingSphere();
    const box = geometry.boundingBox;
    const sphere = geometry.boundingSphere;
    const planar: number[] = [];
    const plane = new Plane(box?.getCenter(new Vector3()).normalize().negate(), sphere?.radius);
    const up = new Vector3(0, 1, 0);
    const vertex = new Vector3();
    const v2 = new Vector3();

    const bigR = buffer.diag;
    // const curve = new EllipseCurve(0, 0, 2 * bigR - 5, 2 * bigR - 5, 0.75 * Math.PI, 1.25 * Math.PI, false, 0);
    const [phiStart, phiEnd] = sliceFn2(pX);
    const curve = new EllipseCurve(-bigR * 0.5, 0, 2 * bigR - 5, 2 * bigR - 5, phiStart, phiEnd, false, 0);
    const curvePoints = curve.getSpacedPoints(NUM_ITEM_POINTS);
    const l2 = new LatheBufferGeometry(curvePoints, NUM_ITEM_POINTS * 2, xStart, xDelta - gutter);
    const l2P = l2.getAttribute('position');
    for (let i = 0; i < position.count; i++) {
      const x = l2P.getX(i);
      const y = l2P.getY(i);
      const z = l2P.getZ(i);
      vertex.set(x, y, z).toArray(planar, planar.length);
    }
    geometry.morphAttributes.position[0] = new Float32BufferAttribute(planar, 3);
    console.log(geometry, plane, phiEnd, phiStart);
    l2.dispose();
  }, [lref], [lref.current, pX]);
  // useRefEffect(([geometry]) => {
  //   const position = geometry.getAttribute('position');
  //   if (!geometry.morphAttributes.position) geometry.morphAttributes.position = [];
  //   geometry.computeBoundingBox();
  //   geometry.boundingSphere ?? geometry.computeBoundingSphere();
  //   const box = geometry.boundingBox;
  //   const sphere = geometry.boundingSphere;
  //   const planar: number[] = [];
  //   const plane = new Plane(box?.getCenter(new Vector3()).normalize().negate(), sphere?.radius);
  //   const up = new Vector3(0, 1, 0);
  //   const vertex = new Vector3();
  //   const v2 = new Vector3();
  //   for (let i = 0; i < position.count; i++) {
  //     const x = position.getX(i);
  //     const y = position.getY(i);
  //     const z = position.getZ(i);
  //     vertex.set(x, y, z);
  //     // plane.projectPoint(vertex, v2);
  //     v2.lerp(vertex, 0.5);
  //     v2.projectOnPlane(up).toArray(planar, planar.length);
  //   }
  //   geometry.morphAttributes.position[0] = new Float32BufferAttribute(planar, 3);
  //   console.log(geometry, plane);
  // }, [lref], [lref.current]);
  // useHelper(mRef, VertexNormalsHelper, 100);
  return (
    <animated.group
      castShadow
      receiveShadow
      ref={ref}
      rotation-x={0}
      // rotation-z={Math.PI}
      rotation-y={rD}
      rotation-z={0}
      // scale={pH}
      scale-x={1}
      scale-y={pH}
      scale-z={1}
      >
      <animated.mesh
        castShadow
        receiveShadow
      // layers={0}
      // quaternion={new Quaternion()}
      // quaternion-w={rM}
      // lookAt={posCenter}
      // quaternion-y={0}
      // quaternion-z={0}
        // morphTargetInfluences={[pos]}
        // morphTargetInfluences-0={pos}
        position-z={rZ}
        ref={mRef}
        rotation-order="ZXY"
        rotation-x={0}
        rotation-y={0}
        rotation-z={Math.PI}
        scale={1}
        >

        <latheBufferGeometry
          args={[points, NUM_ITEM_POINTS * 2, xStart, xDelta - gutter]}
          ref={lref}
          >
          {/* <primitive
            attachObject={['morhpAttributes', 'position']}
            object={[]}
            /> */}
          {/* {morph && (<primitive
            attachObject={['morphAttributes', 'position', '0']}
            object={morph}
            />)} */}
        </latheBufferGeometry>
        <ImageMaterial
          src={src}
          onLoad={afterImageLoads}
          />
      </animated.mesh>
    </animated.group>
  );
}
